      border-radius: 0px 213.5px 213.5px 0px;
      transform: matrix(-1, 0, 0, 1, 0, 0);
      z-index: -1;
    }
  
    .down_blur {
      position: fixed;
      bottom: 0;
      left: 0;
      width: 50%;
      height: 50%;
      border: none;
      background: rgb(103, 103, 103);
      opacity: 0.3;
      border-radius: 0px 213.5px 213.5px 0px;
      z-index: -1;
  
      box-shadow: 140px 270px 20px rgba(103, 103, 103,0.3),
      -50px -200px 50px rgba(103, 103, 103,0.3),
      -50px 0px 250px 20px rgba(103, 103, 103,0.5);
  
  
  
      
    }
  
    .nav {
      width: 100%;
      padding: 0;
      display: flex;
      flex-direction: column;


      justify-content: flex-start;
      align-items: center;
      z-index: 250;
      font-family: Oxygen;
      font-style: normal;
      font-size: 12px;
      line-height: 15px;
      color: #c7c5c5;
  
      .ugli {
        width: 170px;
        border-radius: 10px;
        padding: 5px;
        background-color: #ffffff;
        text-align: center;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
        color: #FF5555;
  
        a {
          color: #FF5555;
          display: block;
        }
      }
  
      ul {
        width: 100%;
        list-style: none;
        margin: 0;
        padding: 0;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;
  
        li {
          cursor: pointer;
          margin: 1em;
        }
  
      }
  
      .design {
        width: 100px;
        font-size: 14px;
        padding: 5px 7px;
        border: 1px solid #ffffff;
        background-color: #ffffff !important;
        color: #FF5555;
        border-radius: 5px;
        z-index: 100;
      }
    }
  
    .text_color {
      color: #FF5555;
    }
  
  
    .uglidesigner {
      width: 70%;
      margin: 0 auto;
      text-align: center;
      z-index: 103;
  
      .uglidesigner_text {
        margin: 3em 0;
        font-family: Oxygen;
  
        h2 {
          font-style: normal;
