          line-height: 23px;
  
        }
  
        .social {
          .icon {
            color: #828282;
            margin: 0 10px;
          }
        }
      }
    }
  
    .portfolio {
      width: 70%;
      margin: 2em auto;
  
      .portfolio_text {
        margin: 1em 0;
  
        h2 {
          margin: 0;
        }
  
        p {
          font-size: 18px;
        }
      }
  
      .card_container {
        display: flex;
        flex-wrap: wrap;
  
        .card {
          background: #C4C4C4;
        }
      }
  
      .view_more {
        background: #FF5555;
