      }
    }
  
  
    .team {
      width: 70%;
      margin: 2em auto;
      text-align: center;
      font-family: Oxygen;
      font-style: normal;
  
      .team_text {
        margin: 1em 0;
  
        h2 {
          margin: 0;
        }
  
        p {
          font-weight: 300;
          font-size: 18px;
          line-height: 23px;
          margin: 0;
        }
      }
  
      .team_container {
        display: flex;
        flex-direction: column;
        margin: 1em auto;
        justify-content: space-between;
  
        .profile {
          z-index: 300;
          margin: 10px;
          background: rgba(255, 255, 255, 0.4);
          border: 2px solid #FF5555;
          box-sizing: border-box;
          backdrop-filter: blur(31px);
          border-radius: 15px;
          padding: 10px 35px;
  
          .profile_img {
            img {}
          }
  
          .profile_text {
            font-family: Oxygen;
            font-style: normal;
            font-weight: 300;
            font-size: 14px;
            line-height: 18px;
            display: flex;
