            text-align: justify;
          }
        }
  
        .profile_contact {
          display: flex;
          justify-content: space-between;
          margin: 0;
  
          p {
            font-family: Oxygen;
            font-style: normal;
            font-weight: 300;
            font-size: 14px;
            line-height: 18p
          }
        }
      }
    }
  
    .contactus {
      width: 70%;
      margin: 2em auto;
      text-align: center;
      font-family: Oxygen;
      font-style: normal;
  
      .contactus_text {
        margin: 1em 0;
  
        h2 {
          margin: 0;
        }
  
        p {
          font-weight: 300;
          font-size: 18px;
          line-height: 23px;
          margin: 0;
        }
      }
  
      form {
        padding: 20px;
        background-color: #ffffff;
        border-radius: 10px;
        width: 60%;
        margin: 0 auto;
        outline: none;
        z-index: 260;
  
        input {
          width: 100%;
          padding: 10px;
          display: block;
          border: 1px solid #FF5555;
          box-sizing: border-box;
          margin: 1em 0;
          background-color: #ffffff;
        }
  
        textarea {
          width: 100%;
          display: block;
          border: 1px solid #FF5555;
          box-sizing: border-box;
          background-color: #ffffff;
          height: 150px;
        }
  
        .submit {
          background: #FF5555;
          color: #FF5555;
        }
      }
    }
  
  
  }
  
}
